.masked-text {
    width: 320px;

    label {
        h3 {
            margin-bottom: 5px;
            float: left;
        }
    }

    .masked-text-field {
        @extend %text_field;
        background: url('./../image/expand_more.svg') no-repeat;
        background-position: 289px 18px;
        @include fontFamily_Montserrat (14px, 400, 24px, left, $dark75);

        &::placeholder {
            color: $dark25;
        }
    }
}