.form-hover-focus {
    width: 320px;

    label {
        h3 {
            margin-bottom: 5px;
            float: left;
        }
    }

    #label-right {
        h3 {
            margin-bottom: 5px;
            font-weight: 400;
            line-height: 14.63px;
            color: $dark50;
            float: right;
        }
    }

    .form-input {
        @extend %text_field;
        @include fontFamily_Montserrat (14px, 400, 24px, left, $dark75);

        &:hover {
            border: 1px solid $dark50;
            border-radius: 4px;
        }

        &:focus {
            border: 1px solid $dark50;
            border-radius: 4px;
        }
    }

}