.subscription-text-field {
    width: 267px;

    label {
        h3 {
            margin-bottom: 5px;
            float: left;
        }
    }

    .form-input {
        @extend %text_field;
        width: 267px;
        background: url('./../image/arrow_forward.svg') no-repeat;
        background-position: 231px 13px;
        @include fontFamily_Montserrat (14px, 400, 24px, left, $dark75);

        &::placeholder {
            color: $dark25;
        }
    }
}