.form-dropdown {
    width: 320px;

    label {
        h3 {
            margin-bottom: 5px;
            float: left;
        }
    }

    .form-input {
        @extend %text_field;
        background: url('./../image/expand_more.svg') no-repeat;
        background-position: 290px 18px;
        @include fontFamily_Montserrat (14px, 400, 24px, left, $dark75);
    }
}